import { handleChange, handleSubmit } from './actions';
import { useEffect, useRef } from "react";

export const FieldComment = ({ userData, setUsers, setState }) => {
  // console.log('userData in RenderCommentInput :>> ', userData);
  const textareaRef = useRef(null);

  const handleInputChange = e => {
    handleChange(setUsers, setState, userData.userId, 'myComment', e.target.value);
  };

  const autoResize = textarea => {
    textarea.style.height = 'auto'; //  
    textarea.style.height = `${textarea.scrollHeight}px`; //   
  };

  useEffect(() => {
    if (textareaRef.current) {
      autoResize(textareaRef.current); //    
    }
  }, [userData.myComment]); //      

  return (
    <div
      style={{
        display: 'flex', //  flexbox
        justifyContent: 'center', //   
        alignItems: 'center', //   
        height: '100%', //  
      }}
    >
      <textarea
        ref={textareaRef}
        placeholder=" "
        value={userData.myComment || ''}
        onChange={e => {
          handleInputChange(e);
          autoResize(e.target);
        }}
        onBlur={() => handleSubmit(userData, 'overwrite')}
        style={{
          // marginLeft: '10px',
          width: '100%',
          // height: 25,
          // minHeight: '40px',
          resize: 'none',
          overflow: 'hidden',
          padding: '5px',
        }}
      />
    </div>
  );
};
