import { handleChange } from './actions';
import { utilCalculateMonthsAgo } from './utilCalculateMonthsAgo';

export const fieldDeliveryInfo = (setUsers, setState, userData) => {
  const { ownKids, lastDelivery, csection } = userData;

  //       ..
  const parseCsectionDate = dateString => {
    //   ..
    const dateRegex = /^\d{2}\.\d{2}\.\d{4}$/;

    if (!dateRegex.test(dateString)) {
      return null; //  null,    
    }

    const [day, month, year] = dateString.split('.').map(Number);

    //    
    const isValidDate = (d, m, y) => d > 0 && d <= 31 && m > 0 && m <= 12 && y > 0 && !isNaN(new Date(y, m - 1, d).getTime());

    if (!isValidDate(day, month, year)) {
      return null; //  null,   
    }

    return dateString;
  };

  //  `csection`    ,  `lastDelivery`  
  const effectiveLastDelivery = lastDelivery || (csection && parseCsectionDate(csection));

  //     `split`
  let deliveryDate = null;
  if (effectiveLastDelivery && /^\d{2}\.\d{2}\.\d{4}$/.test(effectiveLastDelivery)) {
    const [day, month, year] = effectiveLastDelivery.split('.').map(Number);
    deliveryDate = new Date(year, month - 1, day); // JavaScript    0
  }

  const monthsAgo = effectiveLastDelivery ? utilCalculateMonthsAgo(effectiveLastDelivery) : null;

  const whenGetInTouch = deliveryDate ? new Date(deliveryDate.getFullYear(), deliveryDate.getMonth() + 18, deliveryDate.getDate()) : null;

  //     ".."
  const formatDate = date =>
    date
      ? date
          .toLocaleDateString('uk-UA', {
            day: '2-digit',
            month: '2-digit',
            year: 'numeric',
          })
          .replace(/\//g, '.')
      : null;

  if (!ownKids && monthsAgo === null && !csection) return null;

  const parts = [];
  if (effectiveLastDelivery) parts.push(`${effectiveLastDelivery} `);

  if (monthsAgo !== null) {
    if (monthsAgo > 24) {
      const years = Math.floor(monthsAgo / 12); //   
      parts.push(`${years}, `);
    } else {
      parts.push(`${monthsAgo}, `);
    }
  }

  if (ownKids) parts.push(` ${ownKids},`);

  //  
  return (
    <div style={{ display: 'flex', alignItems: 'baseline', gap: '5px' }}>
      {parts.map((part, index) => (
        <span key={`part-${index}`} style={{ whiteSpace: 'nowrap' }}>
          {part}
        </span>
      ))}
      {csection && (
        <button
          key="csection"
          onClick={() => {
            // alert(`C-section: ${formatDate(whenGetInTouch)}`);
            handleChange(setUsers, setState, userData.userId, 'getInTouch', formatDate(whenGetInTouch), true);
          }}
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            backgroundColor: '#28A745',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer',
            fontSize: '16px',
            padding: '0 10px',
          }}
        >
           {csection}
        </button>
      )}
    </div>
  );
};

