import { handleChange, handleSubmit } from './actions';
const { OrangeBtn, UnderlinedInput } = require('components/styles');

export const fieldWriter = (userData, setUsers, setState) => {
  const handleCodeClick = code => {
    let currentWriter = userData.writer || '';
    let updatedCodes = currentWriter?.split(', ').filter(item => item !== code); // ,  
    updatedCodes = [code, ...updatedCodes]; //   
    handleChange(setUsers, setState, userData.userId, 'writer', updatedCodes.join(', '), true);
  };
  return (
    <div style={{ display: 'flex', flexDirection: 'column', gap: '2px', width: '100%' }}>
      <div style={{ display: 'flex', alignItems: 'center', gap: '2px', width: '100%' }}>
        <UnderlinedInput
          type="text"
          // placeholder=" '"
          value={userData.writer || ''}
          onChange={e => handleChange(setUsers, setState, userData.userId, 'writer', e.target.value)}
          onBlur={() => handleSubmit(userData, 'overwrite')}
          style={{
            flexGrow: 1, //   
            maxWidth: '100%', //   
          }}
        />
      </div>

      {/*  :  */}
      <div style={{ display: 'flex', gap: '5px', flexWrap: 'wrap', width: '100%' }}>
        {['IgF', 'IgTT', '', '', 'Ik', '', 'V', 'W', '', ''].map(code => (
          <OrangeBtn
            key={code}
            onClick={() => handleCodeClick(code)}
            style={{
              cursor: 'pointer',
              flex: '1', //      
              width: '25px' /*  ,     */,
              height: '25px' /*  ,     */,
              marginLeft: '5px',
              marginRight: 0,
              color: 'black',
            }}
          >
            {code}
          </OrangeBtn>
        ))}
      </div>
    </div>
  );
};
